.title {
  color: var(--black);
  margin-bottom: 30px;
  font-weight: 600;
}

.items {
  display: grid;

  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 48px;
}

// @media -------------------
@media (max-width: 1199px) {
  .items {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
